
.Card {
    min-width: 100%;
    display: flex;
    flex-direction: row;
    /* height: 50%; */
}

.Card-Text {
    margin: 10vh 0 0 0;
}

h1 {
    font-family: var(--header-font);
}

h3 {
    margin: 0;
    padding: 5px;
    width: 10vh;
}

img {
    max-width: 20vw;
    height: auto;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    margin: 10vh 10vw 10vh 10vw;
}

img:hover {
    animation: bounce 0.5s; /*other browsers*/
    -webkit-animation: bounce 0.5s; /*webkit browsers*/
    -moz-animation: bounce 0.5s; /*firefox*/
    -o-animation: bounce 0.5s; /*opera*/
 }