
#contact-heading {
    font-size: calc(20px + 2vmin);
    color: var(--main-white-color);
    margin: 0;
}

.contact-content {
  width: 100vw;
  display: flex;
  flex-direction: column;
}

  #boxes {
    /* background: #282c34; */
    color: var(--main-white-color);
    text-align: center;
  }
  
  #boxes-upper {
    background: var(--main-white-color);
  }

  svg {
    display: block;
  }

  #boxes {
    background: linear-gradient(270deg, var(--main-red-color), var(--main-white-color), var(--main-blue-color));    
    background-size: 800% 800%;

    -webkit-animation: background 45s ease infinite;
    -moz-animation: background 45s ease infinite;
    -o-animation: background 45s ease infinite;
    animation: background 45s ease infinite;
}

#boxes-upper {
  background: linear-gradient(270deg, var(--main-red-color), var(--main-white-color), var(--main-blue-color));     
  background-size: 800% 800%;

  -webkit-animation: background 45s ease infinite;
  -moz-animation: background 45s ease infinite;
  -o-animation: background 45s ease infinite;
  animation: background 45s ease infinite;
}

@-webkit-keyframes background {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-moz-keyframes background {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@-o-keyframes background {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes background {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}