#nav-div {
    position: fixed;
    right: 0px;
    top: 0px;
    z-index: 999;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.nav-item {
    text-align: right;
    margin: 10px;
    font-family: var(--header-font);
    letter-spacing: 0.2rem;

    -webkit-animation: offHover 0.25s linear;
    -moz-animation: offHover 0.25s linear;
    -o-animation: offHover 0.25s linear;
    animation: offHover 0.25s linear;
}

.nav-item:hover {
    letter-spacing: 0.3rem;

    -webkit-animation: onHover 0.5s linear;
    -moz-animation: onHover 0.5s linear;
    -o-animation: onHover 0.5s linear;
    animation: onHover 0.5s linear;
}

/* Animations */

@-webkit-keyframes onHover {
    0%{letter-spacing: 0.2rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.3rem}
}
@-moz-keyframes onHover {
    0%{letter-spacing: 0.2rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.3rem}
}
@-o-keyframes onHover {
    0%{letter-spacing: 0.2rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.3rem}
}
@keyframes onHover {
    0%{letter-spacing: 0.2rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.3rem}
}

@-webkit-keyframes offHover {
    0%{letter-spacing: 0.3rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.2rem}
}
@-moz-keyframes offHover {
    0%{letter-spacing: 0.3rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.2rem}
}
@-o-keyframes offHover {
    0%{letter-spacing: 0.3rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.2rem}
}
@keyframes offHover {
    0%{letter-spacing: 0.3rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.2rem}
}