@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Oswald:wght@400;600&display=swap);
html {
  scroll-behavior: smooth;

  --main-black-color: #2c2c2c;
  /* --main-white-color: #eeeeee; */
  --main-white-color: #FFFCF9;
  --main-red-color: #EE6352;
  --main-blue-color: #08B2E3;
  --second-white-color: #EFE9F4;
  --main-green-color: #57A773;
  --main-purple-color: #484D6D;

  --header-font: 'Oswald', sans-serif;
  --body-font: 'Open Sans', sans-serif;
  /* --body-font-bold: 'Open Sans', bold; */
}

body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  /* font-family: 'Open Sans', sans-serif;
  font-family: 'Oswald', sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  background: #FFFCF9;
  background: var(--main-white-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  opacity: 1;

  -webkit-animation: fadeIn 2s linear;
  animation: fadeIn 2s linear;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

a {
  color: var(--main-black-color);
  text-decoration: none;
}

/* Animations */

@-webkit-keyframes fadeIn {
  0%{opacity: 0;}
  50%{opacity: 0.5;}
  100%{opacity: 1;}
}
@keyframes fadeIn {
  0%{opacity: 0;}
  50%{opacity: 0.5;}
  100%{opacity: 1;}
}
#nav-div {
    position: fixed;
    right: 0px;
    top: 0px;
    z-index: 999;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.nav-item {
    text-align: right;
    margin: 10px;
    font-family: var(--header-font);
    letter-spacing: 0.2rem;

    -webkit-animation: offHover 0.25s linear;
    animation: offHover 0.25s linear;
}

.nav-item:hover {
    letter-spacing: 0.3rem;

    -webkit-animation: onHover 0.5s linear;
    animation: onHover 0.5s linear;
}

/* Animations */

@-webkit-keyframes onHover {
    0%{letter-spacing: 0.2rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.3rem}
}
@keyframes onHover {
    0%{letter-spacing: 0.2rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.3rem}
}

@-webkit-keyframes offHover {
    0%{letter-spacing: 0.3rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.2rem}
}
@keyframes offHover {
    0%{letter-spacing: 0.3rem}
    50%{letter-spacing: 0.25rem}
    100%{letter-spacing: 0.2rem}
}
